import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isLoading: false, // loading
    loadingState: '1', // '0' , 1' , '2'
    routerViewKey: ''
  },
  mutations: {
    // loadingStatecommit
    SET_LOADING_STATE(state, data) {
      state.isLoading = false
      state.loadingState = data
    }
  },
  actions: {}
})
