<template>
  <div class="swiper-card">
    <swiper :options="getSwiperOption()" ref="refswiper">
      <swiper-slide
        v-for="(slide, index) in swiperData"
        :key="index"
        :style="getWidth(slideWidth)"
        @click.native="click(slide, index)"
      >
        <slot name="slideCard" :item="slide"></slot>
      </swiper-slide>
      <swiper-slide>
        <!--  swiper-slide swiper-slide margin-left: 1rem; -->
      </swiper-slide>
    </swiper>
  </div>
</template>
<script>
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
  name: 'SwiperCard',
  props: {
    // 
    swiperData: {
      type: Array,
      default() {
        return []
      }
    },
    // 
    slideWidth: {
      type: String,
      default: ''
    },
    // 
    spaceBetweenSlide: {
      type: String,
      default: ''
    }
  },
  components: {
    swiper,
    swiperSlide
  },
  data() {
    return {
      swiperOption: {
        slidesPerView: 'auto',
        spaceBetween: 8,
        touchMoveStopPropagation: false, // iscroll truetouchmove
        resistanceRatio: 0 // slide0
      }
    }
  },
  methods: {
    click(item, index) {
      this.$emit('swiperClick', item, index)
    },
    getSwiperOption() {
      this.swiperOption.spaceBetween = parseInt(this.spaceBetweenSlide)
      return this.swiperOption
    },
    getWidth(width) {
      return `width: ${width} !important`
    }
  }
}
</script>
<style lang='scss' scoped>
.swiper-card {
  .swiper-slide {
    width: 33.6%; //  %
    .background-img {
      background-size: 100%;
    }
  }
  .swiper-slide:last-child {
    width: 1%;
  }
}
</style>
